.billing-container {
    max-width: 800px;
    margin: auto;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 20px 20px 20px 20px rgba(0, 0, 0, 0.2);
    margin-top: 10%;
    background: linear-gradient(135deg, rgb(181 131 197) 0%, rgb(111 178 187) 100%);
  }
  .print{
    padding: 10px;
    background-color: #890ea5;
  }
  h2 {
    text-align: center;
  }
  
  .billing-form {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .billing-form select,
  .billing-form input {
    width: 30%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  .billing-form button {
    padding: 10px;
    background-color: #61dafb;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .billing-form button:hover {
    background-color: #21a1f1;
  }
  
  .billing-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
  }
  
  .billing-table th,
  .billing-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: center;
  }
  
  .billing-table th {
    background-color: #f2f2f2;
  }
  
  .billing-table tr:hover {
    background-color: #f1f1f1;
  }
  
  @media (max-width: 600px) {
    .billing-form {
      flex-direction: column;
    }
  
    .billing-form select,
    .billing-form input {
      width: 100%;
      margin-bottom: 10px;
    }
  
    .billing-form button {
      width: 100%;
    }
    .billing-table {
      font-size: 14px; /* Smaller font size for smaller screens */
    }
  
    .billing-table th, .billing-table td {
      padding: 6px; /* Slightly reduced padding */
    }
    .print{
      padding: 4px;
      background-color: #890ea5;
    }
  }
  
  /* Further adjustments for very small screens */
  @media (max-width: 480px) {
    .billing-table {
      font-size: 10px; /* Even smaller font size */
    }
  
    .billing-table th, .billing-table td {
      padding: 4px; /* Further reduce padding */
    }}
  