.navbar {
  background-color: #c591c557;
    padding: 10px;
    position: relative;
    margin: 10px;
    border-radius: 15px;
    display: flex;
    justify-content: end;
}

.menu-button {
  display: none; /* Hidden by default */
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: #450947;
  color: white;
  cursor: pointer;
}

.menu-button:hover {
  background-color: #0056b3;
}

.nav-buttons {
  display: flex;
  flex-direction: column; /* Stack buttons vertically */
  position: absolute; /* Position dropdown below menu button */
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  z-index: 1000; /* Ensure dropdown is on top */
  margin-top: 10px; /* Space between button and dropdown */
}

.nav-buttons button {
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  color: #333;
  cursor: pointer;
}

.nav-buttons button:hover {
  background-color: #f1f1f1;
}

/* Regular navigation buttons for desktop */
.desktop-nav {
  display: flex; /* Show for desktop */
  flex-wrap: wrap; /* Wrap if necessary */
}

.desktop-nav button {
  padding: 10px 15px;
  margin: 5px;
  border: none;
  border-radius: 5px;
  background-color: #350536;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.desktop-nav button:hover {
  background-color: #0056b3;
}

/* Responsive styles */
@media (max-width: 600px) {
  .menu-button {
      display: block; /* Show menu button in mobile */
  }

  .desktop-nav {
      display: none; /* Hide regular nav on mobile */
  }

  .nav-buttons {
      width: 94%; /* Full width on smaller screens */
  }
}
