* {
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Arial', sans-serif;
    background-color: #f0f4f8;
    margin: 0;
    display: flex;
    height: 100vh;
  }
  
  .login-page {
    display: flex;
    justify-content: flex-end; /* Align items to the right */
    align-items: flex-start; /* Align items at the top */
    width: 95%;
    padding: 20px; /* Optional padding */
    position: relative; /* Positioning context for jumping effect */
    padding-top: 10%;
  }
  
  .jumping-effect {
    position: absolute; /* Position it based on the login-page */
    top: 35%; /* Distance from the top */
    left: 110px; /* Distance from the left */
    animation: jump 1s ease-in-out infinite alternate;
  }
  .img {
    border-radius: 50%;

  }
  
  @keyframes jump {
    0% { transform: translateY(0); }
    100% { transform: translateY(-20px); }
  }
  
  .login-container {
    background: linear-gradient(135deg, rgb(133 39 163) 0%, rgb(75 145 155) 100%);
    padding: 2rem;
    border-radius: 10px;
    box-shadow: 20px 20px 20px 20px rgba(0, 0, 0, 0.2);
    width: 30%;
    text-align: center;
  }
  
  .login-title {
    margin-bottom: 1.5rem;
    color: #333;
  }
  
  .error-message {
    color: red;
    margin-bottom: 1rem;
  }
  
  .login-form {
    display: flex;
    flex-direction: column;
  }
  
  .login-input {
    margin: 0.5rem 0;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
  }
  
  .login-input:focus {
    border-color: #007bff;
    outline: none;
  }
  
  .login-button {
    padding: 0.75rem;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s;
  }
  
  .login-button:hover {
    background-color: #0056b3;
  }
  
  /* Responsive Styles */
  @media (max-width: 768px) {
    .login-page {
      justify-content: center; /* Center for mobile */
      align-items: center; /* Center vertically */
    }
  
    .jumping-effect {
      top: 115%; /* Adjust position to be below the login container */
      left: 5%; /* Center it below the login */
    }
  
    .login-container {
      width: 90%; /* Full width for mobile */
      padding: 1.5rem; /* Less padding on mobile */
    }
  
    .login-title {
      font-size: 1.5rem; /* Larger title for visibility */
    }
  
    .login-input {
      font-size: 0.9rem; /* Adjust input size */
    }
  
    .login-button {
      font-size: 0.9rem; /* Adjust button size */
    }
  }
  
  @media (max-width: 480px) {
    .jumping-effect {
      font-size: 1.2rem; /* Even smaller text for very small screens */
    }
  }
  