.view-details-container {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 20px 20px 20px 20px rgba(0, 0, 0, 0.2);
    margin-top: 5%;
    background: linear-gradient(135deg, rgb(181 131 197) 0%, rgb(111 178 187) 100%);
}

.view-details-container h2 {
    text-align: center;
    margin-bottom: 20px;
    color: #333;
}

.billing-table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

.billing-table th, .billing-table td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: left;
}

.billing-table th {
    background-color: #f4f4f4;
    color: #333;
}

.billing-table tbody tr:nth-child(even) {
    background: linear-gradient(135deg, rgb(181 131 197) 0%, rgb(111 178 187) 100%);
}

.billing-table tbody tr:hover {
    background-color: #f1f1f1;
}

.item-details-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

.item-details-table th, .item-details-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.item-details-table th {
    background-color: #e9ecef;
    color: #333;
}

.item-details-table tbody tr:nth-child(even) {
    background-color: #f9f9f9;
}

.item-details-table tbody tr:hover {
    background-color: #f1f1f1;
}
