.add-product-container {
    max-width: 600px;
    margin: 2rem auto;
    padding: 1.5rem;
    border-radius: 8px;
    text-align: center;
    box-shadow: 20px 20px 20px 20px rgba(0, 0, 0, 0.2);
    margin-top: 10%;
    background: linear-gradient(135deg, rgb(181 131 197) 0%, rgb(111 178 187) 100%);
  }
  
  h2 {
    margin-bottom: 1rem;
    font-size: 2rem;
    color: #333;
  }
  
  .error-message {
    color: #ff4d4d;
    margin-bottom: 1rem;
    font-weight: bold;
  }
  
  .add-product-form {
    display: flex;
    flex-direction: column;
  }
  
  .form-input {
    margin: 0.5rem 0;
    padding: 0.75rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 1rem;
  }
  
  .form-input:focus {
    border-color: #007bff;
    outline: none;
  }
  
  .submit-button {
    margin-top: 1rem;
    padding: 0.75rem;
    background-color: #4b105a;;
    color: #ffffff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s ease;
  }
  
  .submit-button:hover {
    background-color: #4b105a;;
  }
  