.edit-product-container {
  max-width: 400px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 20px 20px 20px 20px rgba(0, 0, 0, 0.2);
  margin-top: 10%;
  background: linear-gradient(
    135deg,
    rgb(181 131 197) 0%,
    rgb(111 178 187) 100%
  );
}

.edit-product-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.edit-product-container select,
.edit-product-container input,
.edit-product-container button {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
}

.edit-product-container input:focus,
.edit-product-container select:focus {
  border-color: #007bff;
  outline: none;
}

.edit-product-container button {
  background-color: #964bb7;
  color: white;
  border: none;
  cursor: pointer;
}

.edit-product-container button:hover {
  background-color: rgb(141, 35, 141);
}
