.home-container {
    text-align: center;
    padding: 2rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 10%;
  }
  
  .welcome-text {
    font-size: 2.5rem;
    color: #333;
    animation: jump 0.5s ease-in-out infinite alternate;
    font-family: auto;
  }
  
  .avatar-image {
    border-radius: 50%;
    width: 150px;
    height: 150px;
    transition: filter 1s ease-in; /* Smooth transition for blur effect */
  }
  
  /* Keyframes for animations */
  @keyframes jump {
    from {
      transform: translateY(0);
    }
    to {
      transform: translateY(-10px);
    }
  }
  
  /* Apply the blur effect when the avatar index changes */
  .avatar-image.blur {
    filter: blur(5px);
  }
  